.logo {
  width: 175px;
  height: 31px;
  margin: 16px 24px 16px 0;
  float: left;
}
.logoIcon {
  height: 100%;
  margin-right: 10px;
}

.logoText {
  font-size: 23px;
  font-weight: 600;
  display: inline;
  text-decoration: none;
}

.right {
  float: right;
}

.menuCon {
  line-height: 64px;
  height: 64px;
  float: right;
  width: auto;
}

.menuBar {
  border-bottom: solid 1px #e8e8e8;
  padding-left: 20px;
  padding-right: 20px;
  height: 65px;
}

.iconButton {
  border: none;
}
